  --body-bg-clr: rgb(255, 248, 220);
  --header-text-clr: rgb(20, 20, 20);
  --header-box-shadow: 0 1px 2px 3px var(--header-box-shadow-clr);
  --header-box-shadow-clr: rgb(165, 165, 165);
  --credit-clr: rgb(250, 250, 250);
  --credit-text-shadow: 2px 2px 2px var(--header-box-shadow-clr);
  --form-bg-clr: rgb(255, 242, 215);
  --form-bdr-clr: rgb(123, 63, 0);
  --input-border: 1px solid var(--header-box-shadow-clr);
  --input-shadow: 2px 2px 3px var(--header-box-shadow-clr);
  --book-bg-clr: rgb(128, 0, 0);
  --book-text-clr: rgb(240, 230, 140);
  --book-btn-bdr: 1px solid var(--book-text-clr);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input,
button,
textarea,
select {
  font: inherit;
  font-size: 100%;
  padding: 0;
  margin: 0;
}

body {
  font:
    1.2rem "EB Garamond",
    sans-serif;
  text-align: center;
  background-color: var(--body-bg-clr);
}

.container {
  display: grid;
  grid-template-rows: 200px 100px auto;
}

.header-images {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
  background-repeat: repeat;
  width: 100%;
  position: relative;
  box-shadow: var(--header-box-shadow);
}

.image-credits {
  position: absolute;
  bottom: 15px;
  right: 15px;
  color: var(--credit-clr);
  font-weight: 600;
  text-shadow: var(--credit-text-shadow);
}

.image-credits a {
  color: var(--credit-clr);
}

.header-text h1 {
  font-size: 3rem;
  color: var(--header-text-clr);
  text-shadow: var(--credit-text-shadow);
  margin-top: 15px;
}

#show-form-modal::after {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  content: "";
  width: 100px;
  height: 100px;
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 5;
}

#show-form-modal:hover {
  cursor: pointer;
}

dialog[open] {
  position: fixed;
  margin: auto;
  border: 5px solid var(--form-bdr-clr);
  outline: 2px solid rgb(245, 222, 179);
  border-radius: 10px;
}

form {
  padding: 20px 20px;
  text-align: left;
  background-color: var(--form-bg-clr);
}

form h3 {
  color: var(--header-text-clr);
  text-align: center;
  text-shadow: var(--credit-text-shadow);
}

label {
  display: block;
  width: 100%;
  padding: 3px;
  margin-top: 10px;
  font-weight: 500;
}

label[for="read"] {
  display: inline;
  width: auto;
}

select#read {
  margin: 15px 7px;
  border-radius: 5px;
}

input[type="text"],
input[type="number"] {
  outline: none;
  border-radius: 8px;
  padding: 5px 15px;
  font-size: 1rem;
  font-weight: 500;
  border: var(--input-border);
  width: 300px;
}

input[type="text"]:hover,
input[type="number"]:hover,
select#read:hover {
  box-shadow: var(--input-shadow);
}

input[type="text"]:focus,
input[type="number"]:focus,
select#read:focus {
  outline: 1px solid var(--form-bdr-clr);
  box-shadow: var(--input-shadow);
}


input:invalid, select:invalid{
  border-bottom-color: #ff0000;
  border-bottom-width: 3px;
}

input:focus:invalid{
  outline: none;
}


.form-button {
  display: flex;
  justify-content: space-between;
}

.form-button button {
  padding: 5px 10px;
  width: 100px;
  margin: 20px 0 10px 0;
  background-color: var(--form-bdr-clr);
  color: var(--credit-clr);
  font-weight: 500;
  border: none;
  border-radius: 8px;
}

.form-button button:hover {
  cursor: pointer;
  opacity: 0.8;
  box-shadow: var(--header-box-shadow);
}

.form-button button:active {
  opacity: 1;
  box-shadow: var(--header-box-shadow);
}

.content {
  display: flex;
  flex-wrap: wrap;
  gap: 100px 80px;
  padding: 20px 80px;
  justify-content: center;
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 250px;
  height: 350px;
  padding: 15px;
  color: var(--book-text-clr);
  background-color: var(--book-bg-clr);
  border: 1px solid rgb(82, 82, 82);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  border-left: 20px solid var(--header-text-clr);
  box-shadow: 2px 2px 8px 4px var(--header-box-shadow-clr);
}

.card p:first-child {
  margin-top: 35px;
  font-size: 1.8rem;
  font-weight: 600;
}


.card button.read-toggle {
  position: absolute;
  bottom: -45px;
  color: var(--book-text-clr);
  background-color: var(--header-text-clr);
  padding: 2px 10px;
  border: var(--book-btn-bdr);
  border-radius: 8px;
}

.card button.remove-book {
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 0 5px;
  border-radius: 15px;
  color: var(--book-text-clr);
  background-color: var(--header-text-clr);
  border: var(--book-btn-bdr);
}

.card button.read-toggle:hover,
.card button.remove-book:hover {
  cursor: pointer;
  box-shadow: 0 0 3px 2px var(--header-box-shadow-clr);
}

.card button.remove-book:hover {
  --header-text-clr: rgb(187, 25, 25);
}

.card button.remove-book:active {
  --header-text-clr: rgb(255, 0, 0);
}

.card button.read-toggle:hover {
  --header-text-clr: rgb(56, 56, 56);
}

.card button.read-toggle:active {
  --header-text-clr: rgb(0, 0, 0);
}
